import React, { Component,Fragment } from 'react';
import {Collection} from 'react-materialize'
import LoadingIcon from "../LoadingIcon";
import axios from 'axios'
import UserCard from './UserCard'
class SearchUser extends Component {
    constructor(props){
        super(props);
        this.state={
            usuarios:[]
        }
    }

    getInfoFromAPI(){
        var url = "/api/Usuario";
        
        axios.get(url,{headers:{'Content-Type': 'application/json; charset=utf-8',"X-Requested-With": "XMLHttpRequest"}}).then(res =>{
            console.log(res.data);
            this.setState({usuarios:res.data});
        })
    }

    componentDidMount(){
        this.getInfoFromAPI();
    }


    showUserList=()=>{
        var UserInfos = this.state.usuarios;
        return(
            <Fragment>
                {UserInfos.map(UserInfo =>(
                    <UserCard 
                    key={UserInfo.id}
                    id={UserInfo.id}
                    UserName={UserInfo.nombre} 
                    correo={UserInfo.correo}
                    telefono={UserInfo.telefono}
                    direccion={UserInfo.direccion}
                    handleUpdateUserlist={this.handleUpdateUserlist}
                    />
                ))}
            </Fragment>
        )
    }

    render() {
        if(this.state.usuarios.length===0){
                return(
                    <Fragment>
                        <section style={{ 
                            margin:"400px"
                            }}>
                                <LoadingIcon />
                        </section>
                    </Fragment>
                    )

        }else{
            return (
                <Fragment >
                    <div className="panel ">
                        <div className="Cards center ">
    
                            <Collection 
                            header={<Fragment>
                                    <h4>
                                        {"Lista de usuarios"}
                                    </h4>
                                    </Fragment>}>  
                        <div className="row">
                        {this.showUserList()}  
                        </div>                     
                           </Collection>
                        </div>
                    </div>
    
                </Fragment>
            )
        }
    }
}

export default SearchUser;
