import React, { Component,Fragment } from 'react';
import {Button} from 'react-materialize'
import ModalWindowEditing from './ModalWindowEditing'



class UserCard extends Component {

    handleDeleteCLick =(e)=>{
        e.preventDefault()
        this.props.handleUpdateUserlist(this.props.id);
    }
    render() {
        return (
            <Fragment>
                <div className="col m4">
                    <div className="card horizontal">
                    <div className="card-stacked" >
                        <div className="card-content">
                        <p>
                        {this.props.id} -
                        {this.props.UserName}
                        </p>
                        <p>
                        {this.props.correo}
                        </p>
                        <p>
                        {this.props.telefono}
                        </p>
                        <p>
                        {this.props.direccion}
                        </p>
                        </div>
                        <div className="card-action center">
                        <div className="row">
                                <div className="col l5">
                                        <ModalWindowEditing 
                                              id={this.props.id}
                                              UserName={this.props.UserName} 
                                              correo={this.props.correo}
                                              telefono={this.props.telefono}
                                              direccion={this.props.direccion}
                                        ActionButton={
                                            <Button
                                                    node="button"
                                                    waves="light">
                                                    <i style={{
                                                    padding:"0 20px",
                                                    margin:" 3px 3px auto"
                                                }} className="material-icons">note</i></Button>} />
                                    </div>
                                    <div className="col l5">
                                        <Button
                                        waves="light"
                                        onClick={this.handleDeleteCLick}
                                        className="red">
                                        <i style={{
                                            padding:"0 20px",
                                            margin:"3px 3px auto"
                                        }}
                                        className="material-icons">delete</i>
                                        </Button>
                                        </div>
                                </div>
                        </div>
                    </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default UserCard;
