import React,{Component,Fragment}from 'react';
import {Modal,TextInput,Button} from 'react-materialize'
import axios from 'axios'
import './SingUp.css'


class SingUp extends Component {
    constructor(props){
        super(props);
        this.state={
            nombre:'',
            correo:'',
            telefono:'',
            direccion:'',
            contrasenia:'',
            tipoUsuario:{
                id:2,
                descripcion:"Cliente"
            }
        }
    }
    handleNombre=(e)=>{
        this.setState({
            nombre:e.target.value
        }); 
    }
    handleCorreo=(e)=>{
        this.setState({
            correo:e.target.value
        }); 
    }
    handleTelefono=(e)=>{
        this.setState({
            Telefono:e.target.value
        }); 
    }
    handleDireccion=(e)=>{
        this.setState({
            direccion:e.target.value
        }); 
    }
    handleContrasenia=(e)=>{
        this.setState({
            contrasenia:e.target.value
        }); 
    }
    handleConfirmacionContrasenia=(e)=>{
        
        // this.setState({
        //     ConfirmacionClave:e.target.value
        // }); 
    }

    handleCLickOnSignIn =(e)=>{
        e.preventDefault();
        var url =`http://isaacbretous-001-site1.ctempurl.com/api/Usuario`;
        var Validation ="http://cors-anywhere.herokuapp.com/"+url+"";
        var StateJson = JSON.stringify(this.state);
        console.log(StateJson);
        // console.log("funciono"+ url)
        axios.post(Validation,StateJson,{headers:{'Content-Type': 'application/json; charset=utf-8'}})
        .then((res)=>{
            console.log(res.data)
        });
    }
    render(){
        return(
            <Fragment>
                <Modal
                        actions={[
                            <Fragment >
                                <div  className="btns center" >
                                    <Button style={{margin:'5px'}}node="button" waves="light"
                                            onClick={this.handleCLickOnSignIn}
                                        >Sign In</Button>
                                    <Button modal="close" node="button" waves="light">Close</Button>
                                </div>
                            </Fragment>
                        ]}
                        bottomSheet={false}
                        fixedFooter
                        header="Sign In"
                        id="Modal-SignIn"
                        open={false}
                        options={{
                            dismissible: true,
                            endingTop: '8%',
                            inDuration: 250,
                            onCloseEnd: null,
                            onCloseStart: null,
                            onOpenEnd: null,
                            onOpenStart: null,
                            opacity: 0.5,
                            outDuration: 250,
                            preventScrolling: true,
                            startingTop: '10%'
                        }}
                        trigger={this.props.trigger}
                        >
                        <div className="SignInText">
                                <TextInput
                                    id="SignInNombre"
                                    label="Nombre"
                                    type="text"
                                    onChange={this.handleNombre}
                                />
                                <TextInput
                                    id="SignInEmail"
                                    label="Correo"
                                    type="text"
                                    onChange={this.handleCorreo}
                                />
                                <TextInput
                                    id="SignInApellidos"
                                    label="Telefono"
                                    type="text"
                                    onChange={this.handleTelefono}
                                />
                                <TextInput
                                    id="SignInClave"
                                    label="Direccion"
                                    type="text"
                                    onChange={this.handleDireccion}
                                />
                                <TextInput
                                    id="SignInClave"
                                    label="Clave"
                                    type="password"
                                    onChange={this.handleContrasenia}
                                />
                                <TextInput
                                    id="SignInConfirmaClave"
                                    label="Confirmar clave"
                                    type="password"
                                    onChange={this.handleConfirmacionContrasenia}
                                />

                        </div>
                        </Modal>
        </Fragment>
        );
    }

}

export default SingUp;