import React, { Component,Fragment } from 'react';
import {Modal,Button,TextInput,} from 'react-materialize'
import axios from 'axios'

class ModaWindowCreate extends Component {
    constructor(props){
        super(props);
        this.state={
            nombre:'',
            correo:'',
            telefono:'',
            direccion:'',
            contrasenia:'',
            tipoUsuario:{
                id:1,
                descripcion:"Admin"
            }
        }
    }
    handleNombre=(e)=>{
        this.setState({
            nombre:e.target.value
        }); 
    }
    handleCorreo=(e)=>{
        this.setState({
            correo:e.target.value
        }); 
    }
    handleTelefono=(e)=>{
        this.setState({
            Telefono:e.target.value
        }); 
    }
    handleDireccion=(e)=>{
        this.setState({
            direccion:e.target.value
        }); 
    }
    handleContrasenia=(e)=>{
        this.setState({
            contrasenia:e.target.value
        }); 
    }
    handleConfirmacionContrasenia=(e)=>{
        
        // this.setState({
        //     ConfirmacionClave:e.target.value
        // }); 
    }

    handleCLickOnCrear =(e)=>{
        e.preventDefault();
        var url =`http://isaacbretous-001-site1.ctempurl.com/api/Usuario`;
        var Validation ="http://cors-anywhere.herokuapp.com/"+url+"";
        var StateJson = JSON.stringify(this.state);
        console.log(StateJson);
        // console.log("funciono"+ url)
        axios.post(Validation,StateJson,{headers:{'Content-Type': 'application/json; charset=utf-8'}})
        .then((res)=>{
            console.log(res.data)
        });


    }
    render() {
        return (
            <Modal
                actions={[
                   <Fragment>
                        <Button flat modal="close" node="button" waves="red">Close</Button>
                        <Button flat node="button"
                         onClick={this.handleCLickOnCrear}
                         waves="green">Crear usuario</Button>
                   </Fragment>
                ]}
                bottomSheet={false}
                fixedFooter
                header="Creacion de usuario administrativo"
                id="ModalCrearAdmin"
                open={false}
                options={{
                    dismissible: true,
                    endingTop: '10%',
                    inDuration: 250,
                    onCloseEnd: null,
                    onCloseStart: null,
                    onOpenEnd: null,
                    onOpenStart: null,
                    opacity: 0.5,
                    outDuration: 150,
                    preventScrolling: true,
                    startingTop: '5%'
                }}
                trigger={this.props.ActionButton}
                >
                <div className="row">
                    <div className="col m6">
                        <TextInput
                            id="SignInNombre"
                            label="Nombre"
                            type="text"
                            onChange={this.handleNombre}
                            />
                        </div>
                    <div className="col m6">
                         <TextInput
                            id="SignInEmail"
                            label="Correo"
                            type="text"
                            onChange={this.handleCorreo}
                            />
                    </div>
                    <div className="col m6">
                            <TextInput
                                id="SignInApellidos"
                                label="Telefono"
                                type="text"
                                onChange={this.handleTelefono}
                                />
                    </div>
                    <div className="col m6">
                        <TextInput
                            id="SignInClave"
                            label="Direccion"
                            type="text"
                            onChange={this.handleDireccion}
                            />
                    </div>
                    <div className="col m6">
                                <TextInput
                                    id="SignInClave"
                                    label="Clave"
                                    type="password"
                                    onChange={this.handleContrasenia}
                                />
                    </div>
                    <div className="col m6">
                            <TextInput
                                id="SignInConfirmaClave"
                                label="Confirmar clave"
                                type="password"
                                onChange={this.handleConfirmacionContrasenia}
                                />

                    </div>
                </div>
            </Modal>
        );
    }
}

export default ModaWindowCreate;
