import React,{Component} from 'react';
import NavBar from './componentes/NavBar';
import {BrowserRouter,Route,Switch} from 'react-router-dom';
import 'materialize-css/dist/css/materialize.min.css';
import Footer from './componentes/Footer'
import MiddleImage from './componentes/MiddleImage'
import SearchUser from './componentes/adminPanel/SearchUser'
import './App.css';

class App extends Component{
  constructor(props){
    super(props);
    this.state={
      tipoDeUsuario:'',
      NombreUser:''
      
    }
  }
  handleLoginCoInfo = (NombreUser,tipoDeUsuario) =>{
    
    console.log("Se logeo bien");
    var UserType = (tipoDeUsuario===2) ? "admin":"";
    console.log(UserType)
    this.setState({
      tipoDeUsuario:UserType,
      NombreUser:NombreUser
    }); 
  }
  render(){
    return (
      <BrowserRouter>
            <NavBar 
              typeUser={this.state.tipoDeUsuario}
              NombreUser={this.state.NombreUser}
              handleLoginCoInfo={this.handleLoginCoInfo} 
            />
              <Switch >
              <Route exact path="/">
                  <MiddleImage typeUser={this.state.tipoDeUsuario} />
                </Route>
                <Route path="/AdminData">
                  <MiddleImage typeUser={this.state.tipoDeUsuario} />
                </Route>
                <Route  exact path="/AdminUserList">
                  <SearchUser />  
                </Route>
              </Switch>
            <Footer/>
            
      </BrowserRouter>
  
    );
  }
}


export default App;
