import React, { Component,Fragment } from 'react';
import {HorizontalBar} from 'react-chartjs-2'


class PackgSpeed extends Component {
    constructor(props){
        super(props);
        this.state={
            data : {
                labels: ['Simple','Medium', 'Premiun'],
                datasets: [
                  {
                    label: 'Velocidad',
                    backgroundColor: 'rgba(255,99,132,0.2)',
                    borderColor: 'rgba(255,99,132,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(255,99,132,0.4)',
                    hoverBorderColor: 'rgba(255,99,132,1)',
                    data: [this.props.SimpleSpeed, this.props.MediumPlanSpeed, this.props.PremiumPlanSpeed  ]
                  }
                ]
              }
        }
    }

      
    render() {
        return (
            <Fragment>
                    <h4>Planes</h4>
                    <HorizontalBar data={this.state.data} />
            </Fragment>
        );
    }
}

export default PackgSpeed;