import React, { Component,Fragment } from 'react';
import {Modal,Button,TextInput} from 'react-materialize'

class ModalWindowEditing extends Component {
    constructor(props){
        super(props);
        this.state={
            id:'',
            nombre:'',
            correo:'',
            telefono:'',
            direccion:'',
            contrasenia:'',
            tipoUsuario:{
                id:2,
                descripcion:"Cliente"
            }
        }
    }

    handleChangeOnName =(e)=>{
        this.setState({
            nombre:e.target.value
        })
    }
    handleChangeOnCorreo =(e)=>{
        this.setState({
            correo:e.target.value
        })
    }
    handleChangeOntelefono =(e)=>{
        this.setState({
            telefono:[e.target.value]
        })
    }
    handleChangeOnDireccion =(e)=>{
        this.setState({
            direccion:e.target.value
        })
    }
    componentDidMount(){
        this.setState({
            id:this.props.id,
            nombre:this.props.UserName,
            correo:this.props.correo,
            telefono:this.props.telefono,
            direccion:this.props.direccion,
        });
    }
    render() {
        return (
            <Modal
                actions={[
                   <Fragment>
                        <Button flat modal="close" node="button" waves="red">Close</Button>
                        <Button flat node="button" waves="green" onClick={this.handleCLick}>Aplicar Cambios</Button>
                   </Fragment>
                ]}
                bottomSheet={false}
                fixedFooter
                header="Detalles del cliente"
                id="ModalEditarUsuario"
                open={false}
                options={{
                    dismissible: true,
                    endingTop: '10%',
                    inDuration: 250,
                    onCloseEnd: null,
                    onCloseStart: null,
                    onOpenEnd: null,
                    onOpenStart: null,
                    opacity: 0.5,
                    outDuration: 250,
                    preventScrolling: true,
                    startingTop: '4%'
                }}
                trigger={this.props.ActionButton}
                >
                <div className="row">
                    <div className="col l12">
                        <TextInput
                        value={this.state.nombre}
                        onChange={this.handleChangeOnName}
                        placeholder="Nombre de usuario"/>
                    </div>
                    <div className="col l12">
                        <TextInput 
                        value={this.state.correo}
                        onChange={this.handleChangeOnCorreo}
                        placeholder="Correo electronico"/>
                    </div>
                    <div className="col l12">
                        <TextInput 
                        value={this.state.telefono}
                        onChange={this.handleChangeOntelefono}
                        placeholder="Telefono"/>
                    </div>
                    <div className="col l12">
                        <TextInput
                        value={this.state.direccion}
                        onChange={this.handleChangeOnDireccion}
                        placeholder="Direccion"/>
                    </div>
                </div>
                </Modal>
        );
    }
}

export default ModalWindowEditing;
