import React, { Component,Fragment } from 'react';
import {Doughnut} from 'react-chartjs-2'

class MostBoughtPackg extends Component {
    constructor(props){
        super(props);
        this.state={

            data : {
                labels: [
                    'Simple',
                    'Medium',
                    'Premiun'
                ],
                datasets: [{
                    data: [this.props.comprasPlanes[0],this.props.comprasPlanes[1],this.props.comprasPlanes[2]],
                    backgroundColor: [
                    '#FF6384',
                    '#36A2EB',
                    '#FFCE56'
                    ],
                    hoverBackgroundColor: [
                    '#FF6384',
                    '#36A2EB',
                    '#FFCE56'
                    ]
                }]
            },
            MasComprado:''
        }
    }
    componentDidMount(){
        var Mayor = parseInt( Math.max(this.props.comprasPlanes[0],this.props.comprasPlanes[1],this.props.comprasPlanes[2]));
        this.setState({
            MasComprado:Mayor
        })
    }
    render() {
        return (
            <Fragment>
                {/* {this.state.MasComprado} */}
                    <h4>Plan mas comprado es el: Simple</h4>
                    <Doughnut data={this.state.data} />
            </Fragment>
        );
    }
}

export default MostBoughtPackg;
