import React,{Component,Fragment}from 'react';
import {Modal,TextInput,Button} from 'react-materialize'
import axios from 'axios'
import './Login.css';

class LogIn extends Component {
    constructor(props){
        super(props);
        this.state={
            correo:'',
            contrasenia:''
        }
    }
    handleUserName=(e)=>{
        this.setState({
            correo:e.target.value
        }); 
    }
    handlePassword = (e) =>{
        this.setState({
            contrasenia:e.target.value
        }); 
    }
    hanldeLogInCLick =(e)=>{
        e.preventDefault();
        var url ="/api/LogIn";
        var StateJson = JSON.stringify(this.state);
        console.log(StateJson)
        axios.post(url,StateJson,{headers:{'Content-Type': 'application/json; charset=utf-8',"X-Requested-With": "XMLHttpRequest"}})
        .then((res)=>{
            var UserName = res.data.nombre;
            var UserType = res.data.tipoUsuario.id;
            this.props.handleLoginCoInfo(UserName,UserType);
        }).catch((err)=>{
            console.log("Problemas : " + err);
            console.log(this.state);
        })
    }

    render(){
        return(
            <Fragment>
                <Modal
                        actions={[
                            <Fragment >
                                <div  className="btns center" >
                                    <Button style={{margin:'5px'}}node="button" waves="light" onClick={this.hanldeLogInCLick}>Log In</Button>
                                    <Button modal="close" node="button" waves="light">Close</Button>
                                </div>
                            </Fragment>
                        ]}
                        bottomSheet={false}
                        fixedFooter
                        header="Log In"
                        id="Modal-Login"
                        open={false}
                        options={{
                            dismissible: true,
                            endingTop: '20%',
                            inDuration: 250,
                            onCloseEnd: null,
                            onCloseStart: null,
                            onOpenEnd: null,
                            onOpenStart: null,
                            opacity: 0.5,
                            outDuration: 250,
                            preventScrolling: true,
                            startingTop: '10%'
                        }}
                        trigger={this.props.trigger}
                        >
                        <div className="LoginText">
                            <TextInput
                                id="LoginEmail"
                                label="Email o Usuario"
                                type="text"
                                onChange={this.handleUserName}
                                />
                                <TextInput
                                id="password"
                                type="password"
                                label="Clave"
                                onChange={this.handlePassword}
                                />
                            </div>
                        </Modal>
            </Fragment>
        );
    }

}

export default LogIn;