import React,{Component,Fragment} from 'react';
import 'materialize-css'
import {Navbar,Icon,Dropdown} from 'react-materialize'
import ModaWindowCreate from './adminPanel/ModaWindowCreate'
import Login from './Login/LogIn'
import SingUp from './Login/SingUp'
import {Link} from 'react-router-dom'


class NavBar extends Component{
    render(){
        if(this.props.typeUser==="Admin" || this.props.typeUser==="admin" ){
            return(
                <Fragment>
                    <Navbar
                            className="white"
                            brand={
                            <Link className="brand-logo center" to="/AdminData">Admin</Link>
                            }
                            id="mobile-nav"
                            menuIcon={<Icon>menu</Icon>}
                            options={{
                                draggable: true,
                                edge: 'left',
                                inDuration: 200,
                                onCloseEnd: null,
                                onOpenEnd: null,
                                onOpenStart: null,
                                outDuration: 200,
                                preventScrolling: false
                            }}>
                            <Link to="/">
                                Log - Out
                            </Link>
                                <Link to="/AdminData">
                                    Ver datos
                                </Link>

                            <Dropdown
                                id="UserOptionsDropDown"
                                options={{
                                alignment: 'left',
                                autoTrigger: true,
                                closeOnClick: true,
                                constrainWidth: true,
                                container: null,
                                coverTrigger: true,
                                hover: false,
                                inDuration: 150,
                                onCloseEnd: null,
                                onCloseStart: null,
                                onOpenEnd: null,
                                onOpenStart: null,
                                outDuration: 250
                                }}
                                trigger={<Link to='/AdminUserList'>Usuarios</Link>}>
                                <ModaWindowCreate ActionButton={<Link to='/'>Crear usuario</Link>}/>
                                <Link to="/AdminUserList">
                                Lista de usuarios
                                </Link>
                            </Dropdown>
                            {/* <Link to="/">
                                facturas
                            </Link>   */}
                            {/* <Link to='/'>
                                paquetes
                            </Link> */}
                    </Navbar>
                </Fragment>
            );
        }else if(this.props.typeUser==="Cliente" || this.props.typeUser==="cliente" ){
            return(
                <Fragment>
                    <Navbar
                            alignLinks="center"
                            className="white"
                            brand={<Link className="brand-logo center" to="/">Cliente</Link>}
                            id="mobile-nav"
                            menuIcon={<Icon>menu</Icon>}
                            options={{
                                draggable: true,
                                edge: 'left',
                                inDuration: 250,
                                onCloseEnd: null,
                                onCloseStart: null,
                                onOpenEnd: null,
                                onOpenStart: null,
                                outDuration: 200,
                                preventScrolling: true
                            }}
                            >
                            <Link to="/">
                                Log - Out
                            </Link>
                    </Navbar>
                </Fragment>
            );
        }else {
            return(
                <Fragment>
                    <Navbar
                            alignLinks="right"
                            className="white"
                            brand={<Link className="brand-logo center" to="/">Bienvenido</Link>}
                            id="mobile-nav"
                            menuIcon={<Icon>menu</Icon>}
                            options={{
                                draggable: true,
                                edge: 'left',
                                inDuration: 250,
                                onCloseEnd: null,
                                onCloseStart: null,
                                onOpenEnd: null,
                                onOpenStart: null,
                                outDuration: 200,
                                preventScrolling: true
                            }}
                            >
                            <SingUp trigger={<Link to="/" >Sig In</Link>} />
                            <Login 
                            handleLoginCoInfo={this.props.handleLoginCoInfo}
                            trigger={<Link to="/" >Log In</Link>} />
                    </Navbar>
                </Fragment>

            );
        }
    }
}
export default NavBar;