import React, { Component, Fragment, } from 'react';
import DashBoard from './charts/DashBoard'
import LoadingIcon from "../LoadingIcon";
import "./Adminpanel.css";
import axios from 'axios'

class AdminPanel extends Component {
    constructor(props){
        super(props);
        this.state={
            CantidadUsuarios:[]
        }

    }
    componentDidMount(){
        var url = "/api/Usuario";
        axios.get(url,{ "crossDomain": "true", }).then(res =>{
            console.log(res.data);
            this.setState({
                CantidadUsuarios:res.data
            })
        })
    }

    render() { 
        if(this.state.CantidadUsuarios.length===0){
            return(
                <Fragment>
                    <section style={{ 
                        margin:"400px"
                        }}>
                            <LoadingIcon />
                    </section>
                </Fragment>
                )
        }else{        
            return (
            <Fragment>
                <DashBoard CantidadUsuarios={this.state.CantidadUsuarios.length}/>
            </Fragment>
        
        )
    }

    }
}
export default AdminPanel;